<template>
  <div class="hello">
    <div class="container-fluid mt-4">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Pic</th>
            <th scope="col">First</th>
            <th scope="col">Last</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(value,key) in memberdata" :key="key" :class="{'bg-warning':value.selected}">
            <td>{{key+1}}</td>
            <td>
              <img :src="value.picture.large" alt="會員圖片" width="50">
            </td>
            <td>{{value.name.first}}</td>
            <td>{{value.name.last}}</td>
            <td>
              <button class="btn btn-primary" @click="getbt(value,value.name.first)">確認</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>


<script>
export default {
  name: "HelloWorld",
  props: {
    getonline: {
      type: String,
      require: true
    }
  },
  data() {
    return {
      memberdata: "",
      setuersename: "",
    };
  },
  methods: {  
    getbt(item,name) {
      this.memberdata.forEach(element => {
        if(element!=item)
        element.selected=false;
      });
      item.selected = !item.selected;
      this.$emit("setname", name);
    }
  },
  mounted() {
    this.axios.get("https://randomuser.me/api/?results=20").then(e => {
      this.memberdata = e.data.results;
      this.memberdata.forEach(element => {
        this.$set(element, "selected", false);
      });
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



