  </button>
  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item">
      <router-link to="Number" class="nav-link" tag="a">NumberList</router-link>
      </li>
      <li class="nav-item"><router-link to="map" class="nav-link" tag="a">googlemap</router-link></li>
    </ul>
    <div>Hi,{{Name}}</div>
  </div>
</nav>


    <router-view :getonline="online" @setname="getName"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
           online:'上線',
           Name:'',
    }
  },
  methods:{
    getName(data){
      this.Name=data;
    }
  }

}
</script>

