<template>
  <div id="map">
    <GmapMap
      id="maps"
      ref="mapRef"
      :center="center"
      :zoom="mapinfo.zoom"
      :map-type-id="mapinfo.type"
      :options="options"
      class="mapszie"
      @center_changed="draggmap"
      @dragend="mapend"
    >
      <GmapMarker :position="center"/>
    </GmapMap>
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg-6 p-4 text-center" v-for="(data,index) in restaurantinfo" :key="index">
          <img :src="imgsrc[index]" :alt="error" width="100%" height="400">
          <h3>{{data.name}}</h3>
          <h5
            class="text-primary"
          >{{getphone[index]}}</h5>
          <h4>{{getopenhour[index]}}</h4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      center: {
        lat: 25.04,
        lng: 121.422
      },
      curcenter: {
        lat: 0,
        lng: 0
      },
      mapinfo: {
        zoom: 16,
        type: "roadmap"
      },
      options: {
        zoomControl: true,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: true,
        disableDefaultUi: false
      },
      mapdata: "",
      apikey: "AIzaSyAUvhCLXVSIgxExvQW_bUsifz_cPt5fLok",
      restaurantinfo: "",
      restaurantdetail: []
    };
  },
  methods: {
    draggmap(center) {
      this.curcenter.lat = center.lat();
      this.curcenter.lng = center.lng();
    },
    mapend() {
      this.center.lat = this.curcenter.lat;
      this.center.lng = this.curcenter.lng;
      this.getinfo();
    },
    error() {
      return "無圖片";
    },
    getinfo() {
      let vm = this;
      this.restaurantdetail = [];
      let foodapiurl = encodeURIComponent(
        `https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=${
          this.center.lat
        },${this.center.lng}&radius=200&type=restaurant&key=${this.apikey}`
      );
      let promise = new Promise(function(resolve, reject) {
        vm.axios
          .get("https://jsonp.afeld.me/?url=" + foodapiurl)
          .then(function(res) {
            console.log(res.data.results);
            if (res.data.results == "") {
              vm.restaurantinfo=[];
            } 
            else {
              res.data.results.map((element, index, arr) => {
                let fooddetails = encodeURIComponent(
                  `https://maps.googleapis.com/maps/api/place/details/json?placeid=${
                    element.place_id
                  }&fields=name,rating,formatted_phone_number,opening_hours,photos&key=${
                    vm.apikey
                  }`
                );
                vm.axios
                  .get("https://jsonp.afeld.me/?url=" + fooddetails)
                  .then(function(res) {
                    console.log(2);
                    vm.restaurantdetail.push(res.data.result);
                    if (index == arr.length - 1) resolve(arr);
                  });
              });
            }
          });
      });
      promise.then(function(res) {
        vm.restaurantinfo = res;
      });
    }
  },
  computed: {
    imgsrc() {
      return this.restaurantinfo.map(e => {
        if (!e.photos) {
          return "https://upload.wikimedia.org/wikipedia/commons/thumb/4/44/Emoji_u1f21a.svg/1200px-Emoji_u1f21a.svg.png";
        } else {
          let photo = e.photos[0].photo_reference;
          let url = `https://maps.googleapis.com/maps/api/place/photo?maxwidth=400&photoreference=${photo}&key=${
            this.apikey
          }`;
          return url;
        }
      });
    },
    getphone(){
      return this.restaurantdetail.map(e=>
      {
           return e.formatted_phone_number!==undefined?e.formatted_phone_number:"無電話";
      });
    },
    getopenhour(){
      let week = new Date();
      
       let today=(week.getDay() + 6) % 7;
      return this.restaurantdetail.map(e=>{
       return e.opening_hours!==undefined?e.opening_hours.weekday_text[today]:"無資訊";
      });
    }
  },
  mounted() {
    this.getinfo();
  }
};
</script>

<style scoped>
.mapszie {
  width: 100%;
  padding: 0;
  margin: 0;
  height: 600px;
}
</style>



