import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import googleMap from '@/components/googleMap'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/map'
    },
    {
      path: '/Number',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/map',
      name: 'googleMap',
      component: googleMap
    }
  ]
})



